<template>
	<div class="personal">
		<view class="personalTop">
			<top-view> </top-view>
		</view>
		<view class="">
			<personInpt @okpersonaldata="okpersonaldata" ref='getChild' :nationList="nationList"
				:userSexList="userSexList"></personInpt>
		</view>

	</div>
</template>

<script>
	import webViewBridge from '@/components/guojun-web-view-bridge/guojun-web-view-bridge.nvue';
	import topView from '@/components/personal';
	import {
		dictsearch
	} from "@/api/index.js";
	import personInpt from '@/components/personal/enterpriseinput';
	export default {
		components: {
			topView,
			personInpt
		},
		data() {
			return {
				count: 0,
				fromH5Message: '',
				nationList: [],
				userSexList: [],
			};
		},
		onShow(e) {

		},
		methods: {
			okpersonaldata() {
				uni.showToast({
					title: "提交成功"
				})
				uni.switchTab({
					url: '/pages/tab/index/index'
				})
			},

			getSex() {
				let params = {
					dictTag: "性别|民族"
				}
				dictsearch(params).then(res => {
					if (res.code == 200) {
						let sexMinzu = res.data
						for (let key in sexMinzu) {
							if (key == "民族") {
								this.nationList = sexMinzu[key]
							} else if (key == "性别") {
								this.userSexList = sexMinzu[key]
							}

							console.log(key + '---' + sexMinzu[key])
						}

					}


				})
			},
			triggerH5() {
				this.$refs.webViewBridge.callHandler('test', {
					toH5Data: '给H5的参数' + Date.now()
				}, data => {
					this.fromH5Message = JSON.stringify(data);
				});
			},
			defaultHandler() {
				this.$refs.webViewBridge.send({
						fff: '1000.0'
					},
					function(data) {
						uni.showToast({
							title: 'send' + JSON.stringify(data),
							icon: 'none'
						});
					}
				);
			},
			reciveMessage(evt) {
				console.log("---1-->", evt)
			},
			recivePostMessage(evt) {
				console.log("-2---->", evt)
			},
		},
		onLoad() {
			this.getSex()
		},
	};
</script>

